.all{
  display: block;
}

*{
  font-family: 'product_sans';
}

.container {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: fit-content;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: fit-content;
  padding-bottom: 50px;
}

.card {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  width: 300px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: block; /* Updated to display as block */
  text-align: center; /* Added text alignment */
  padding: 20px; /* Increased padding for better spacing */
  box-shadow: rgba(0, 0, 0, 0.423) 0px 0px 5px; /* Added box-shadow */
}

.card.selected {
  background-color: #f0f0f0;
}

.card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.card-body {
  margin-bottom: 20px; /* Increased margin for better spacing */
}

.vote-button {
  background-color: #4CAF50;
  box-shadow: black 0px 0px 5px;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 100px;
  width: 200px;
  height: 70px;
  font-size: 20px;
  font-weight: bold;
}

.vote-button:hover {
  background-color: #45a049;
}
.button-container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0px;
}