/* App.css */

/* Global Styles */
*{
  font-family: 'product_sans';
}
.welcome-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-size: cover;
  background-position: center;
}


.welcome-page h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.welcome-page p {
  font-size: 16px;
  margin-bottom: 20px;
}

.welcome-page button {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.welcome-page button:hover {
  background-color: #0056b3;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'product_sans';
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.navbar {
  background-color: #ffffff;
  padding: 20px;
  text-align: center;
}

.navbar-title {
  margin: 0;
}

.main-content {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.voting-container {
  text-align: center;
}

.message {
  font-size: 24px;
  margin-bottom: 20px;
}

/* VotingOptions.css */

.option-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  width: 300px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.option-card:hover {
  background-color: #f0f0f0;
}

.option-card.selected {
  background-color: #f0f0f0;
}

.option-card.selected .option-image {
  border: 2px solid red;
}
.navbar-logo{
  height: 100px;
  padding: 10px;
  padding-right: 20px;
}
.option-image {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
  border-radius: 50%;
  border: 2px solid transparent;
  transition: border-color 0.3s ease;
}

.option-title {
  font-size: 18px;
  margin-bottom: 5px;
}

.option-description {
  font-size: 14px;
  color: #666;
}

/* VoteResults.css */

.vote-results {
  margin-top: 20px;
}

.vote-results h2 {
  margin-top: 0;
}

.vote-results p {
  margin-bottom: 5px;
}


.fullscreen-message {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
}

.fullscreen-message h2 {
  color: white;
  font-size: 24px;
  text-align: center;
}

nav{
  display: flex;
  justify-content: center;
  align-items: center;
  height: fit-content;
  padding-bottom: 50px;
  background-color: white;
}